<template>
  <div class="box">
    <div class="ckData" style="margin-top: 2%;">
      <el-row :gutter="250" style="margin-left: 100px;margin-right: 100px;">
        <el-col :span="6" style="background-color: #409EFF;">
          <div>
            <el-statistic :value="ZqdyjInData.totalCount" title="CK" style="width: 50px;"></el-statistic>
          </div>
        </el-col>
        <el-col :span="6" style="background-color: #F56C6C;">
          <div>
            <el-statistic :value="ZqdyjInData.hotCount" title="" style="width: 50px;"></el-statistic>
          </div>
        </el-col>
        <el-col :span="6" style="background-color: #67C23A;">
          <div>
            <el-statistic :value="ZqdyjInData.noHelp" title="" style="width: 50px;"></el-statistic>
          </div>
        </el-col>
        <el-col :span="6" style="background-color: #909399;">
          <div>
            <el-statistic :value="ZqdyjInData.hasHelp" title="" style="width: 50px;"></el-statistic>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="help">
      <div style="text-align:center;margin: 10px;"></div>
      <el-form :model="zqdyjForm" ref="zqdyjForm" :rules="rules" label-width="100px">
        <el-form-item label="" prop="account">
          <el-input v-model="zqdyjForm.account" clearable placeholder="ck"></el-input>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input v-model="zqdyjForm.remark" clearable placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="isTime">
          <el-switch v-model="zqdyjForm.isTime"></el-switch>
        </el-form-item>
      </el-form>
      <div style="margin-top:10px;text-align: center;">
        <el-button type="primary" :loading="goLoading" @click="GO('zqdyjForm')"></el-button>
      </div>
    </div>

    <div class="log">
      <div style="text-align:center;margin: 10px;"></div>
      <div class="msg">
        <div v-for="(log, index) in logs" :key="index">
          <span v-if="log.indexOf('') != -1" style="color:greenyellow">{{ log }}</span>
          <span v-else>{{ log }}</span>
        </div>
      </div>
    </div>

    <div class="assistList">
      <div style="text-align:center;font-size:18px;font-weight:600"></div>
      <el-table :data="assistList" style="width: 100%">
        <el-table-column prop="remark" label="()" align="center"></el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scoped">{{ scoped.row.status | status }}</template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scoped">{{ scoped.row.helpStatus | helpStatus }}</template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scoped">
            <el-button type="text" :loading="scoped.row.deleteLoading" @click="deleteLine(scoped.row.id)"></el-button>
            <el-button type="text" @click="help(scoped.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import { Message } from "element-ui";
import {
  zqdyjHelp,
  zqdyjHelp2,
  getZqdyjData,
  getZqdyjdelete
} from "../axiosRequest/allRequest";
export default {
  filters: {
    status(value) {
      const obj = {
        0: "",
        1: ""
      };
      return obj[value];
    },
    helpStatus(value) {
      const obj = {
        0: "",
        1: ""
      };
      return obj[value];
    }
  },
  data() {
    return {
      zqdyjForm: {
        account: "",
        remark: "",
        isTime: false
      },
      logs: [],
      goLoading: false,
      websock: null,
      getZqdyjIn: null,
      ZqdyjInData: {},
      assistList: [],
      rules: {
        account: [
          { required: true, message: "ck", trigger: "blur" }
        ],
        remark: [{ required: true, message: "", trigger: "blur" }],
        isTime: []
      },
    };
  },

  created() {
    this.initWebSocket();
  },
  mounted() {
    getZqdyjData().then(res => {
      if (res.code == 200) {
        this.ZqdyjInData = res.data;
        let a = JSON.parse(JSON.stringify(res.data.helpList));
        a.forEach((item, index) => {
          this.$set(item, "deleteLoading", false);
        });
        this.assistList = a;
      }
    });
    this.getZqdyjIn = setInterval(() => {
      getZqdyjData().then(res => {
        if (res.code == 200) {
          this.ZqdyjInData = res.data;
          let a = JSON.parse(JSON.stringify(res.data.helpList));
          a.forEach((item, index) => {
            this.$set(item, "deleteLoading", false);
          });
          this.assistList = a;
        }
      });
    }, 60 * 1000);
  },
  destroyed() {
    debugger
    this.websock.close(); //websocket
    clearInterval(this.getZqdyjIn);
    this.getZqdyjIn = null;
  },

  methods: {
    GO(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.logs = [];
          this.goLoading = true;
          zqdyjHelp(this.zqdyjForm).then(res => {
            this.goLoading = false;
            if (res.code != 200) {
              Message.error(res.message);
            } else {
              Message.success(res.message);
              getZqdyjData().then(res => {
                if (res.code == 200) {
                  this.ZqdyjInData = res.data;
                  let a = JSON.parse(JSON.stringify(res.data.helpList));
                  a.forEach((item, index) => {
                    this.$set(item, "deleteLoading", false);
                  });
                  this.assistList = a;
                }
              });
            }
          });
        } else {
          return false;
        }
      });
    },

    deleteLine(id) {
      this.assistList.forEach((item, index) => {
        if ((item.id = id)) {
          this.$set(item, "deleteLoading", true);
        }
      });
      getZqdyjdelete({ id: id }).then(res => {
        this.assistList.forEach((item, index) => {
          if ((item.id = id)) {
            this.$set(item, "deleteLoading", false);
          }
        });
        if (res.code == 200) {
          Message.success(res.message);
          getZqdyjData().then(res => {
            if (res.code == 200) {
              this.ZqdyjInData = res.data;
              let a = JSON.parse(JSON.stringify(res.data.helpList));
              a.forEach((item, index) => {
                this.$set(item, "deleteLoading", false);
              });
              this.assistList = a;
            }
          });
        } else {
          Message.error(res.message);
        }
      });
    },
    help(row) {
      zqdyjHelp2({ id: row.id, helpCode: row.helpCode, ck: row.ck, remark: row.remark }).then(res => {
        if (res.code == 200) {
          Message.success(res.message);
          getZqdyjData().then(res => {
            if (res.code == 200) {
              this.ZqdyjInData = res.data;
              let a = JSON.parse(JSON.stringify(res.data.helpList));
              a.forEach((item, index) => {
                this.$set(item, "deleteLoading", false);
              });
              this.assistList = a;
            }
          });
        } else {
          Message.error(res.message);
        }
      });
    },
    initWebSocket() {
      //weosocket
      let token = window.sessionStorage.getItem("robotToken");
      debugger
      // debugger
      let ipport = window.location.host;
      const wsuri = "ws://" + ipport + "/robot/ZqdyjWebSocket/" + token;
      this.websock = new WebSocket(wsuri);
      this.websock.onmessage = this.websocketonmessage;
      // this.websock.onopen = this.websocketonopen;
      this.websock.onerror = this.websocketonerror;
      // this.websock.onclose = this.websocketclose;
    },
    websocketonopen() {
      //send
    },
    websocketonerror() {
      //
      this.initWebSocket();
    },
    websocketonmessage(e) {
      //
      // debugger
      this.logs.push(e.data);
    },
    websocketsend(Data) {
      //
      this.websock.send(Data);
    },
    websocketclose(e) {
      //
      console.log("", e);
    }
  }
};
</script>

<style lang="less" scoped>
.box {
  display: flex;
  flex-wrap: wrap;
}

.ckData {
  display: flex;
  flex: 100%;
  margin-bottom: 50px;
}

.help {
  flex: 30%;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 10px;
  border-radius: 10px;
  overflow: hidden;
}

.log {
  flex: 30%;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 10px;
  margin-left: 10px;
  border-radius: 10px;

  .msg {
    height: 50vh;
    overflow-y: auto;
  }
}

.assistList {
  flex: 30%;
  padding: 10px;
  box-shadow: 2px 2px 5px #888888;
}

::v-deep .el-input__inner {
  border: 1px solid black;

  &::-webkit-input-placeholder {
    color: rgb(98, 97, 97);
  }

  .el-input__suffix {
    color: black;
  }
}

::v-deep .el-form-item__label {
  color: black;
}

.logs {
  width: 100%;
  height: 400px;
  background-color: pink;
  overflow: hidden;
}
</style>