<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle"></div>
      <div class="rightButton">
        <el-input placeholder="//" v-model="code" clearable class="input-with-select">
          <el-button slot="append" icon="el-icon-search" @click="getList"></el-button>
        </el-input>
        <el-button type="primary" size="mini" @click="openParamter('')"></el-button>
      </div>
    </div>

    <div class="mainMseeage">
      <div class="ChooseAll" v-if="ShowChooseAll">
        <el-button type="primary" :loading="batchDeleteLoading" @click="batch()"></el-button>
        <span style="margin-left: 10px">
          
          <span style="color: #409eff">{{ choosedNum }}</span>
        </span>
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        :height="tableHeight"
        :loading="tableLoading"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
            <span>{{(pageNo-1)*pageSize+scope.$index+1}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="codeName"
          label=""
          width="200"
          align="center"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column prop="value" label="" :show-overflow-tooltip="true" align="center"></el-table-column>
        <el-table-column prop="createdTime" label="" width align="center"></el-table-column>
        <el-table-column prop="updatedTime" label="" width align="center"></el-table-column>

        <el-table-column prop label="" width fixed="right" align="center">
          <template slot-scope="scoped">
            <el-tooltip class="item" effect="dark" content="" placement="top">
              <el-button
                type="text"
                class="operationButton"
                @click="openParamter('', scoped.row)"
              >
                <i class="el-icon-edit" style="color: #409eff"></i>
              </el-button>
            </el-tooltip>

            <!-- <el-tooltip class="item" effect="dark" content="" placement="top">
                            <el-button type="text" class="operationButton" @click="startEnv(scoped.row)"><i
                                    class="el-icon-circle-check" style="color: #409eff"></i>
                            </el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="" placement="top">
                            <el-button type="text" class="operationButton" @click="forbiddenEnv(scoped.row)"><i
                                    class="el-icon-remove-outline" style="color: #409eff"></i>
                            </el-button>
            </el-tooltip>-->

            <el-tooltip class="item" effect="dark" content="" placement="top">
              <el-button
                type="text"
                :loading="scoped.row.deleteLoading"
                class="operationButton"
                @click="deleteEnv(scoped.row)"
              >
                <i class="el-icon-delete" style="color: #409eff"></i>
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNo"
        :page-sizes="[10, 20, 30]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableTotal"
      ></el-pagination>
    </div>

    <el-dialog
      :title="paramterName"
      :visible.sync="addVisible"
      width="30%"
      @close="handleClose('addForm')"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addForm">
        <el-form-item label="" prop="code">
          <el-input
            v-model="addForm.code"
            :disabled="paramterName == ''"
            clearable
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="codeName">
          <el-input v-model="addForm.codeName" clearable placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="value">
          <el-input v-model="addForm.value" clearable placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addVisible = false"> </el-button>
        <el-button type="primary" :loading="addLoading" @click="addParamter('addForm')"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { Message } from "element-ui";
import {
  querySystemParams,
  addSystemParam,
  updateSystemParam,
  deleteSystemParams
} from "../axiosRequest/allRequest";

export default {
  data() {
    return {
      code: "",
      ShowChooseAll: false,
      // tableHeight: window.innerHeight,
      tableHeight: window.innerHeight - 120,
      pageNo: 1,
      pageSize: 10,
      tableTotal: 0,
      choosedNum: 0,
      paramterName: "",
      addVisible: false,
      addLoading: false,
      tableData: [],
      tableLoading: false,
      addForm: {
        value: "",
        codeName: "",
        code: ""
      },
      addFormRules: {
        code: [{ required: true, message: "", trigger: "blur" }],
        // value: [{ required: true, message: "", trigger: "blur" }],
        value: [],
        codeName: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      multipleSelection: [],
      batchDeleteLoading: false
    };
  },
  created() {
    this.getList();
  },
  mounted() {},
  methods: {
    getList() {
      this.tableLoading = true;
      let param = {
        code: this.code,
        pageNo: this.pageNo,
        pageSize: this.pageSize
      };
      querySystemParams(param).then(res => {
        this.tableLoading = false;
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          let a = res.data.list;
          a.forEach((item, index) => {
            this.$set(item, "deleteLoading", false);
          });
          this.tableData = a;
          this.tableTotal = res.data.total;
        }
      });
    },

    handleSelectionChange(val) {
      this.multipleSelection = val;
      this.choosedNum = val.length;
      if (val.length > 0) {
        this.ShowChooseAll = true;
      } else {
        this.ShowChooseAll = false;
      }
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.pageNo = val;
      this.getList();
    },
    handleClose(formName) {
      this.$refs[formName].resetFields();
    },
    addParamter(val) {
      this.$refs[val].validate(valid => {
        if (valid) {
          this.addLoading = true;
          if (this.paramterName == "") {
            updateSystemParam(this.addForm).then(res => {
              this.addLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success(res.message);
                this.addVisible = false;
                this.getList();
              }
            });
          } else if (this.paramterName == "") {
            addSystemParam(this.addForm).then(res => {
              this.addLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success(res.message);
                this.addVisible = false;
                this.getList();
              }
            });
          }
        } else {
          return false;
        }
      });
    },

    openParamter(val, row) {
      this.addVisible = true;
      this.paramterName = val;
      if (row) {
        this.addForm.code = row.code;
        this.addForm.codeName = row.codeName;
        this.addForm.value = row.value;
      }
    },

    batch() {
      let deleteId = [];
      this.multipleSelection.forEach(item => {
        deleteId.push(item.id);
      });
      this.batchDeleteLoading = true;
      deleteSystemParams(deleteId).then(res => {
        this.batchDeleteLoading = false;
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          Message.success(res.message);
          this.getList();
        }
      });
    },

    startEnv(row) {
      //
      this.$confirm("xxx?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        Message.success("111");
      });
    },
    forbiddenEnv(row) {
      //
      this.$confirm("xxx?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        Message.success("222");
      });
    },
    deleteEnv(row) {
      //
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.tableData.forEach(item => {
          if (item.id == row.id) {
            this.$set(item, "deleteLoading", true);
          }
        });
        deleteSystemParams([row.id]).then(res => {
          this.tableData.forEach(item => {
            if (item.id == row.id) {
              this.$set(item, "deleteLoading", false);
            }
          });
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            Message.success(res.message);
            this.getList();
          }
        });
      });
    }
  }
};
</script>
    
<style lang="less" scoped>
.el-radio-group {
  width: 100%;
}
</style>