<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle"></div>
      <div class="rightButton">
        <el-input placeholder="" v-model="key" class="input-with-select">
          <el-button slot="append" icon="el-icon-search" @click="getList"></el-button>
        </el-input>
        <!-- <el-button type="primary" size="mini" @click="addNewTask"></el-button> -->
      </div>
    </div>
    <div class="mainMseeage">
      <!-- <div class="ChooseAll" v-if="ShowChooseAll">
        <el-button type="primary" @click="batch()"></el-button>
        <el-button type="primary" @click="batch()"></el-button>
        <el-button type="primary" @click="batch()"></el-button>
        <el-button type="primary" @click="batch()"></el-button>
        <el-button type="primary" @click="batch()"></el-button>
        <el-button type="primary" @click="batch()"></el-button>
        <el-button type="primary" @click="batch()"></el-button>
        <el-button type="primary" @click="batch()"></el-button>
        <span style="margin-left: 10px"><span style="color: #409eff">{{ choosedNum }}</span></span>
      </div>-->
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        v-loading="tableLoading"
        :height="tableHeight"
      >
        <!-- <el-table-column type="selection" width="55"> </el-table-column> -->
        <el-table-column label="" width="80">
          <template slot-scope="scope">
            <span>{{(changePage-1)*changeSize+scope.$index+1}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="" width align="center">
          <template slot-scope="scoped">
            <el-button type="text">{{ scoped.row.name }}</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="command" label="" show-overflow-tooltip align="center"></el-table-column>
        <el-table-column prop label="" width="400" fixed="right" align="center">
          <template slot-scope="scoped">
            <el-button type="text" @click="fdTask(scoped.row, 4)"></el-button>
            <el-button type="text" @click="fdTask(scoped.row, 5)"></el-button>
            <el-button type="text" @click.native="fdTask(scoped.row, 2)"></el-button>
            <el-button type="text" @click.native="fdTask(scoped.row, 1)"></el-button>
            <el-button type="text" @click.native="fdTask(scoped.row, 3)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="changePage"
        :page-sizes="[10, 20, 30]"
        :page-size="changeSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableTotal"
      ></el-pagination>
    </div>

    <!--  -->
    <el-dialog
      :title="newTitle"
      :visible.sync="newTaskVisible"
      width="30%"
      @close="handleClose('newTaskForm')"
    >
      <el-form :model="newTaskForm" :rules="newTaskrules" ref="newTaskForm" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input v-model="newTaskForm.name" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="command">
          <el-input v-model="newTaskForm.command" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="timingRule">
          <el-input v-model="newTaskForm.timingRule" placeholder="()     " clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input v-model="newTaskForm.remark" placeholder="" clearable></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="newTaskVisible = false"> </el-button>
        <el-button type="primary" @click="addNewTask('newTaskForm')"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog :title="qlName" :visible.sync="qlChooseVisible" width="30%" @close="qlChooseClosed">
      <el-checkbox
        :indeterminate="isIndeterminate"
        v-model="qlCheckAll"
        @change="qlChooseAllChange"
      ></el-checkbox>
      <div style="margin: 15px 0"></div>
      <el-checkbox-group v-model="qlChooseList" @change="listChange">
        <el-checkbox v-for="(qlName, index) in qlList" :label="qlName.id" :key="index">
          {{
          "url" + qlName.url + " " + "" + qlName.status
          }}
        </el-checkbox>
      </el-checkbox-group>

      <span slot="footer" class="dialog-footer">
        <el-button @click="qlChooseVisible = false"> </el-button>
        <el-button type="primary" :loading="sureQlChooseLoading" @click="sureQlChoose"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="journalName"
      :visible.sync="journalVisible"
      width="30%"
      :class="taskJournal"
      @close="journalClose"
    >
      <p v-for="(item, index) in taskJournalList" :key="index">{{ item }}</p>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="journalVisible = false"></el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { Message } from "element-ui";
import {
  queryScripts,
  runScript,
  stopScript,
  queryQl,
  disableScript,
  pinScript,
  enableScript
} from "../axiosRequest/allRequest";

export default {
  data() {
    return {
      tableLoading: false,
      key: "",
      tableHeight: window.innerHeight - 120,
      ShowChooseAll: false,
      changePage: 1,
      changeSize: 10,
      tableTotal: 0,
      choosedNum: 0,
      newTaskVisible: false,
      newTitle: "",
      newTaskForm: {
        name: "",
        command: "",
        timingRule: "",
        remark: ""
      },
      tableData: [],
      newTaskrules: {
        name: [],
        remark: [],
        command: [
          { required: true, message: "", trigger: "blur" }
        ],
        timingRule: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      journal: "",
      journalVisible: false,
      taskJournal: "taskJournalOk",
      //   taskJournal: "taskJournaling",
      taskJournalList: [1, 2, 3],
      //
      taskInterval: null,
      qlList: [],
      qlChooseVisible: false,
      isIndeterminate: true,
      qlCheckAll: false,
      qlChooseList: [],
      lineRow: {},
      sureQlChooseLoading: false,
      qlName: "",
      qlType: 1,
      journalName: ""
    };
  },
  created() {
    this.getList();
    queryQl().then(res => {
      if (res.code != 200) {
        Message.error("");
      } else {
        this.qlList = res.data;
      }
    });
  },
  mounted() {},
  methods: {
    qlChooseAllChange(val) {
      this.qlChooseList = [];
      if (val) {
        this.qlList.forEach((item, index) => {
          this.qlChooseList.push(item.id);
        });
      }
      this.isIndeterminate = val;
    },
    listChange(val) {
      let checkedCount = val.length;
      this.qlCheckAll = checkedCount === this.qlList.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.qlList.length;
    },
    sureQlChoose() {
      if (this.qlChooseList.length == 0)
        return Message.warning("");
      let param = {
        ids: [...this.qlChooseList],
        command: this.lineRow.command
      };
      this.sureQlChooseLoading = true;
      if (this.qlType == 5) {
        stopScript(param).then(res => {
          this.sureQlChooseLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            this.qlChooseVisible = false;
            this.journalVisible = true;
            this.taskJournalList = res.data;
            this.journalName = "";
          }
        });
      } else if (this.qlType == 4) {
        runScript(param).then(res => {
          this.sureQlChooseLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            this.qlChooseVisible = false;
            this.journalVisible = true;
            this.taskJournalList = res.data;
            this.journalName = "";
          }
        });
      } else if (this.qlType == 3) {
        pinScript(param).then(res => {
          this.sureQlChooseLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            this.qlChooseVisible = false;
            this.journalVisible = true;
            this.taskJournalList = res.data;
            this.journalName = "";
          }
        });
      } else if (this.qlType == 2) {
        enableScript(param).then(res => {
          this.sureQlChooseLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            this.qlChooseVisible = false;
            this.journalVisible = true;
            this.taskJournalList = res.data;
            this.journalName = "";
          }
        });
      } else if (this.qlType == 1) {
        disableScript(param).then(res => {
          this.sureQlChooseLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            this.qlChooseVisible = false;
            this.journalVisible = true;
            this.taskJournalList = res.data;
            this.journalName = "";
          }
        });
      }
    },
    qlChooseClosed() {
      this.lineRow = {};
      this.qlChooseList = [];
    },
    getList() {
      this.tableLoading = true;
      let param = {
        key: this.key,
        pageNo: this.changePage,
        pageSize: this.changeSize
      };
      queryScripts(param).then(res => {
        this.tableLoading = false;
        this.tableData = [];
        this.tableTotal = 0;
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          this.tableData = res.data.scriptsList;
          this.tableTotal = res.data.total;
        }
      });
    },

    handleSelectionChange(val) {
      this.choosedNum = val.length;
      if (val.length > 0) {
        this.ShowChooseAll = true;
      } else {
        this.ShowChooseAll = false;
      }
    },
    handleSizeChange(val) {
      //
      this.changeSize = val;
      this.getList();
    },
    handleCurrentChange(val) {
      //
      this.changePage = val;
      this.getList();
    },
    handleClose(formName) {
      this.$refs[formName].resetFields();
    },
    addNewTask(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          return false;
        }
      });
    },

    fdTask(row, i) {
      this.qlChooseVisible = true;
      this.lineRow = row;
      this.qlChooseList = [];
      this.qlType = i;
      if (i == 1) {
        this.qlName = "";
      } else if (i == 2) {
        this.qlName = "";
      } else if (i == 3) {
        this.qlName = "";
      } else if (i == 4) {
        this.qlName = "";
      } else if (i == 5) {
        this.qlName = "";
      }
      this.qlList.forEach(item => {
        this.qlChooseList.push(item.id);
      });
      this.listChange(this.qlChooseList);
    },

    //
    start() {
      this.taskInterval = setInterval(() => {
        //
        this.taskJournalList.push(1);
        if (1) {
          //
          //   this.end();
        }
      }, 1000);
    },

    end() {
      clearInterval(this.taskInterval);
      this.taskInterval = null;
    },

    detialTask(row) {
      this.journalVisible = true;
      this.journal = row.name;
      this.start();
    },
    journalClose() {
      this.end();
    },
    addNewTask() {
      this.newTaskVisible = true;
      this.newTitle = "";
    },

    editTask(row) {
      console.log(111);
      this.newTaskVisible = true;
      this.newTitle = "";
      //row
    },
    deleteTask(row) {},
    topTask(row) {}
  },
  //  
  beforeDestroy() {
    clearInterval(this.taskInterval);
  }
};
</script>

<style lang="less" scoped>
.el-pagination {
  background-color: #fff;
  text-align: right;
  padding: 10px;
}

.el-dropdown-link {
  color: #409eff;
  cursor: pointer;
}

.tagNoLoading::before {
  content: "";
  font-family: element-icons;
  font-size: 15px;
  width: 10px;
  height: 10px;
  margin-right: 10px;
}

::v-deep .taskJournaling {
  .el-dialog__title::before {
    content: "";
    width: 10px;
    /*loading*/
    height: 10px;
    margin-right: 10px;
    display: inline-block;
    /*loading100%*/
    border: 2px solid #f3f3f3;
    /**/
    border-top: 2px solid #4eacff;
    /**/
    border-radius: 50%;
    /**/
    animation: loading-360 0.8s infinite linear;
  }
}

::v-deep .taskJournalOk {
  .el-dialog__title::before {
    content: "";
    font-family: element-icons;
    width: 10px;
    /*loading*/
    height: 10px;
    margin-right: 10px;
  }
}

.tagLoading::before {
  content: "";
  width: 10px;
  /*loading*/
  height: 10px;
  margin-right: 10px;
  display: inline-block;
  /*loading100%*/
  border: 1px solid #f3f3f3;
  /**/
  border-top: 1px solid #4eacff;
  /**/
  border-radius: 50%;
  /**/
  animation: loading-360 0.8s infinite linear;
}

@keyframes loading-360 {
  0% {
    transform: rotate(0deg);
    /*0*/
  }

  100% {
    transform: rotate(360deg);
    /*360*/
  }
}
</style>
